import { Fragment, useEffect } from "react";
import { connect } from "react-redux";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Row, Col, Select } from "antd";
import {
  RiCheckboxCircleLine,
  RiAlertLine,
  RiInformationLine,
} from "react-icons/ri";
import NumericInput from "../../../../components/data-entry/inputs/numberInput";
import {
  isTempraroryModelVisible,
  TempraroryInputChangeValue,
  stockTempraroryAdd,
  getPurchaseById,
  getFormulaExecuter,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

import {
  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
  getAllCounters,
  getMiniCounters
} from "../../../../../store/mini/miniSlice";

const addTemprarory = (props) => {
  const [form] = Form.useForm();
  const { Option } = Select;

  useEffect(() => {
    props.getAllCounters();
    props.getPurchaseAvailableStock({ ...props.purchaseAvailableStockParams });
    props.getMiniCounters();
  }, []);

  function ChangeHandeler(e) {
    props.TempraroryInputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  function vehicleDetails(purchaseId) {
    props.purchaseAvaliableStockList.forEach((purchase) => {
      if (purchase.id == purchaseId) {
        props.getPurchaseById(purchase);
      }
    });
  }


  function checkCounter() {
    if (props.tempraroryData?.counter_id == props.tempraroryData.tocounter_id) {
      Modal.error({
        icon: (
          <span className="remix-icon">
            <RiAlertLine />
          </span>
        ),
        title: <h5 className="hp-mb-0 hp-font-weight-500">Error</h5>,
        content: (
          <div>
            <p className="hp-p1-body hp-text-color-black-80">
              Sorry! Not allowd to same counter
            </p>
          </div>
        ),
      });
      props.InputChangeValue({
        key: "tocounter_id",
        value: null,
      });
    }
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      requestername: props.tempraroryData?.requestername,
      counter_id: props.tempraroryData.counter_id,
      tocounter_id: props.tempraroryData?.tocounter_id,
      purchase_id: props.tempraroryData?.purchase_id,
      description: props.tempraroryData?.description,
    };
    props.stockTempraroryAdd(obj);
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const obj = {
      id: props.tempraroryData?.id,
      requestername: props.tempraroryData?.requestername,
      counter_id: props.tempraroryData.counter_id,
      tocounter_id: props.tempraroryData?.tocounter_id,
      purchase_id: props.tempraroryData?.purchase_id,
      description: props.tempraroryData?.description,
    };
    props.stockTempraroryAdd(obj);
  };

  // Reset Input Values on Close

  const handleResetInputValues = () => {
    props.isTempraroryModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.tempraroryData.id == null || props.tempraroryData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.tempraroryData.id == null || props.tempraroryData.id == 0
        ? "Add"
        : "Update"}{" "}
      Temporary
    </h5>
  );

  // const onSearch = (value) => {
  //   console.log("search:", value);
  // };

  useEffect(() => {
    form.setFieldsValue({
      requestername: props.tempraroryData.requestername,
      counter_id: props.tempraroryData.counter_id,
      tocounter_id: props.tempraroryData.tocounter_id,
      purchase_id: props.tempraroryData.purchase_id,
      vehicleno: props.purchaseDetails?.vehicle?.vehicleno,
      description: props.tempraroryData.description,
    });
  }, [props.tempraroryData]);

  return (
    <Modal
      visible={props.addTempraroryModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isTempraroryModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={616}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.tempraroryData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row>
          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Requester Name"
              name="requestername"
              rules={[
                {
                  required: true,
                  message: "Please Input Requester Name",
                },
                {
                  pattern: new RegExp(/^[a-zA-Z][a-zA-Z\s]+$/i),
                  message: "field does not accept numbers",
                },
              ]}
              hasFeedback
            >
              <Input
                id="requestername"
                name="requestername"
                style={{ width: "100%" }}
                placeholder="Requester Name"
                value={props.tempraroryData.requestername}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="From Counter"
              name="counter_id"
              rules={[
                {
                  required: true,
                  message: "Please Select From Counter",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select From Counter"
                optionFilterProp="Counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onBlur={(value) => {
                  props.getPurchaseAvailableStock({
                    ...props.purchaseAvailableStockParams,
                    counter: props.tempraroryData?.counter_id,
                    page: 1,
                  });
                  props.setPurchaseAvailableStockParams({
                    ...props.purchaseAvailableStockParams,
                    counter: props.tempraroryData?.counter_id,
                    page: 1,
                  });
                }}
                onChange={(value) => {
                  // props.GetCounterByID(value);
                  props.getPurchaseAvailableStock();
                  props.TempraroryInputChangeValue({
                    key: "counter_id",
                    value: value,
                  });
                }}
                value={
                  props.tempraroryData.counter_id == null
                    ? ""
                    : props.tempraroryData?.counter_id
                }
              >
                {props.miniCountersList.map((counter, index) => {
                  return (
                    <Option key={index} value={counter.id}>
                      {counter.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>


          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="To Counter"
              name="tocounter_id"
              rules={[
                {
                  required: true,
                  message: "Please Select To Counter",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select To Counter"
                optionFilterProp="Counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onBlur={() => {
                  checkCounter();
                }}
                onChange={(value) => {
                  props.TempraroryInputChangeValue({
                    key: "tocounter_id",
                    value: value,
                  });
                }}
                value={
                  props.tempraroryData.tocounter_id == null
                    ? ""
                    : props.tempraroryData?.tocounter_id
                }
              >
                {props.allCountersList.map((tocounter, index) => {
                  return (
                    <Option key={index} value={tocounter.id}>
                      {tocounter.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          {/* <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="To Counter"
              name="tocounter_id"
              rules={[
                {
                  required: true,
                  message: "Please Select To Counter",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select To Counter"
                optionFilterProp="Counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.TempraroryInputChangeValue({
                    key: "tocounter_id",
                    value: value,
                  });
                }}
                value={
                  props.tempraroryData.tocounter_id == null
                    ? ""
                    : props.tempraroryData?.tocounter_id
                }
              >
                {props.allCountersList.map((tocounter, index) => {
                  return (
                    <Option key={index} value={tocounter.id}>
                      {tocounter.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col> */}

          {props.tempraroryData.id == null ? (
            <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
              <Form.Item
                label="Vehicle Number"
                name="vehicle_id"
                rules={[
                  {
                    required: true,
                    message: "Please Input Vehicle Number",
                  },
                ]}
                hasFeedback
              >
                <Select
                  showSearch
                  placeholder="Select vehicle "
                  optionFilterProp="vehicle"
                  filterOption={false}
                  onSearch={(value) => {
                    props.setPurchaseAvailableStockParams({
                      ...props.purchaseAvailableStockParams,
                      page: 1,
                      search: value,
                    });
                    props.getPurchaseAvailableStock({
                      ...props.purchaseAvailableStockParams,
                      page: 1,
                      search: value,
                    });
                  }}
                  onChange={(value) => {
                    vehicleDetails(value);
                    props.TempraroryInputChangeValue({
                      key: "purchase_id",
                      value: value,
                    });
                  }}
                  onPopupScroll={() => {
                    if (
                      props.purchaseAvailableStockParams.page <
                      props.purchaseAvailableStockParams.no_of_pages
                    ) {
                      props.setPurchaseAvailableStockParams({
                        ...props.purchaseAvailableStockParams,
                        page: props.purchaseAvailableStockParams.page + 1,
                      });
                      props.getPurchaseAvailableStock({
                        ...props.purchaseAvailableStockParams,
                        page: props.purchaseAvailableStockParams.page + 1,
                      });
                    }
                  }}
                  value={
                    props.tempraroryData.vehicle_id == null
                      ? ""
                      : props.tempraroryData?.vehicle_id
                  }
                >
                  {props.purchaseAvaliableStockList.map((purchase, index) => {
                    return (
                      <Option key={index} value={purchase.id}>
                        {purchase?.vehicle.vehicleno} - {purchase?.code}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
          ) : (
            <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
              <Form.Item label="Vehicle Number" name="vehicleno">
                <Input
                  id="vehicleno"
                  name="vehicleno"
                  style={{ width: "100%" }}
                  placeholder="Vehicle Number"
                  value={props.purchaseDetails?.vehicle?.vehicleno}
                  disabled
                />
              </Form.Item>
            </Col>
          )}

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Vehicle Info">
              <Input
                id="vehicleVersion"
                name="vehicleVersion"
                style={{ width: "100%" }}
                placeholder="Vehicle Info"
                value={props.purchaseDetails?.vehicle?.version?.name}
                disabled
              />
            </Form.Item>
          </Col>

          <Col md={24} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Remarks"
              name="description"
              rules={[
                {
                  required: true,
                  message: "Please Input Remarks",
                },
              ]}
              hasFeedback
            >
              <Input.TextArea
                id="description"
                name="description"
                style={{ width: "100%" }}
                placeholder="Write Remarks..."
                value={props.tempraroryData.description}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ stockRequest, mini }) {
  return {
    tempraroryData: stockRequest.tempraroryData,
    addTempraroryModel: stockRequest.addTempraroryModel,
    allCountersList: mini.allCountersList,
    purchaseDetails: stockRequest.purchaseDetails,
    purchaseAvaliableStockList: mini.purchaseAvaliableStockList,
    purchaseAvailableStockParams: mini.purchaseAvailableStockParams,
    miniCountersList: mini.miniCountersList,
  };
}

export default connect(mapStateToProps, {
  isTempraroryModelVisible,
  TempraroryInputChangeValue,
  stockTempraroryAdd,
  getAllCounters,
  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
  getPurchaseById,
  getFormulaExecuter,
  getMiniCounters
})(addTemprarory);
