import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  List,
  Empty,
  Button,
  Input,
  Card,
  Pagination,
  Spin,
} from "antd";
import { Search } from "react-iconly";
import {
  OrderedListOutlined,
  AppstoreOutlined,
  PlusOutlined,
  FilterOutlined,
  UploadOutlined,
} from "@ant-design/icons";
import PageTitle from "../../../../../layout/components/content/page-title";
import EmptyImage from "../../../../../assets/images/app/empty-list.svg";
import { getReportList } from "../../../../../store/reports/reportsSlice";
import {
  getStockTempraroryList,
  setStockTempraroryParams,
  isTempraroryModelVisible,
  isTempraroryDrawerVisible,
  TempraroryFilterInputChangeValue,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import { isMobile } from "react-device-detect";
import StockTransferTempraroryList from "./StockTransferTempraroryList";
import StockTransferTempraroryGrid from "./StockTransferTempraroryGrid";
import StockTransferTempraroryModel from "./StockTransferTempraroryModel";
import AddTemprarory from "./addTemprarory";
import Filter from "./Filters/Filter";
import moment from "moment";
function stockTransferTemprarory(props) {
  const { Search } = Input;

  useEffect(() => {
    props.getStockTempraroryList({
      ...props.stockTempraroryparams,
      page: 1,
      search: "",
      page_size: 10,
    });
  }, []);

  const [productListToggle, setProductListToggle] = useState(false);

  function onPageChange(page, pageSize) {
    props.setStockTempraroryParams({
      ...props.stockTempraroryparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getStockTempraroryList({
      ...props.stockTempraroryparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.setStockTempraroryParams({
      ...props.stockTempraroryparams,
      search: val,
    });
    props.getStockTempraroryList({
      ...props.stockTempraroryparams,
      search: val,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const onChange = (key) => {
    setActiveTab(key);
    if (key == "1") {
      props.getStockTempraroryList({
        page: 1,
        search: "",
        page_size: 10,
      });
    } else {
      props.getStockTempraroryList({
        page: 1,
        search: "",
        page_size: 10,
      });
    }
    console.log(key);
  };

  function exportData() {
    // var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: 0,
      model_name: "TempraroryTransfer",
    };

    props.getReportList({
      objData,
      ParamsData: {
        ...props.stockTempraroryparams,
        start_date:
          props.stockTempraroryparams.start_date != null &&
          props.stockTempraroryparams.start_date != ""
            ? moment(props.stockTempraroryparams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.stockTempraroryparams.end_date != null &&
          props.stockTempraroryparams.end_date != ""
            ? moment(props.stockTempraroryparams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
      },
    });
  }

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="Stock Transfer Temporary " goBack={false} />

      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          <Col span={24} xs={24} sm={24} md={
              props.userAccessList?.indexOf("Purchase.export_purchase") !== -1
                ? 12
                : 15
            }
            className="hp-ecommerce-app-header-search "
          >
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getStockTempraroryList({
                    ...props.stockTempraroryparams,
                    page: 1,
                    search: "",
                  });
                  props.setStockTempraroryParams({
                    ...props.stockTempraroryparams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>
          <Col span={24} xs={4} sm={4}  md={3}>
            <Button
              onClick={() => {
                props.isTempraroryDrawerVisible(true);
                // props.TempraroryFilterInputChangeValue({
                //   key: "start_date",
                //   value: moment(new Date(y, m, 1)),
                // });
              }}
              className="hp-w-100 hp-p-12"
              icon={<FilterOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col>

          {props.userAccessList?.indexOf(
            "StockTransfer.add_temprarorytransfer"
          ) !== -1 && (
            <Col span={24} xs={4} sm={4} md={4}>
              <Button
                onClick={() => {
                  props.isTempraroryModelVisible(true);
                }}
                className="hp-w-100 hp-p-12"
                icon={<PlusOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Add Temporary" }
              </Button>
            </Col>
          )}

          {props.userAccessList?.indexOf(
            "StockTransfer.export_temprarorytransfer"
          ) !== -1 && (
            <Col span={24} xs={4} sm={4}  md={3}>
              <Button
                onClick={() => {
                  exportData();
                }}
                className="hp-w-100 hp-p-12 "
                icon={<UploadOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Export"}
              </Button>
            </Col>
          )}

          <Col span={24} xs={6} sm={5} md={1}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<AppstoreOutlined />}
              onClick={() => setProductListToggle(false)}
            ></Button>
          </Col>

          <Col span={24} xs={6} sm={5} md={1}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<OrderedListOutlined />}
              onClick={() => setProductListToggle(true)}
            ></Button>
          </Col>
        </Row>
      </Col>

      <Col
        span={24}
        xs={24}
        className="hp-purchase-app-content"
        productListToggle={productListToggle}
        setProductListToggle={setProductListToggle}
      >
        <Card>
          <Spin tip="loading" spinning={props.loading} delay={500}>
            {props.stockTempraroryList.length !== 0 ? (
              <>
                {!productListToggle ? (
                  <>
                    <List
                      dataSource={props.stockTempraroryList}
                      renderItem={(value) => (
                        <StockTransferTempraroryGrid value={value} />
                      )}
                    />
                    <Pagination
                      size="small"
                      current={props.stockTempraroryparams?.page}
                      pageSize={props.stockTempraroryparams?.page_size}
                      total={props.stockTemprarorylistCount}
                      showTotal={showTotal}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </>
                ) : (
                  <>
                    <StockTransferTempraroryList
                      stockTransferList={props.stockTempraroryList}
                      // deletePurchase={deleteRecord}
                    />{" "}
                    <Pagination
                      size="small"
                      current={props.stockTempraroryparams?.page}
                      pageSize={props.stockTempraroryparams?.page_size}
                      total={props.stockTemprarorylistCount}
                      showTotal={showTotal}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </>
                )}
              </>
            ) : (
              <Card>
                <Empty
                  className="hp-m-32"
                  image={EmptyImage}
                  imageStyle={{
                    height: 160,
                  }}
                  description={
                    <h5>Your Stock Transfer Temporary list is empty</h5>
                  }
                ></Empty>
              </Card>
            )}
          </Spin>
        </Card>
      </Col>
      <StockTransferTempraroryModel />
      <AddTemprarory />
      <Filter />
    </Row>
  );
}
function mapStateToProps({ stockRequest, system }) {
  return {
    stockTempraroryList: stockRequest.stockTempraroryList,
    stockTempraroryparams: stockRequest.stockTempraroryparams,
    noofpages: stockRequest.noofpages,
    stockTemprarorylistCount: stockRequest.stockTemprarorylistCount,
    loading: stockRequest.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getStockTempraroryList,
  setStockTempraroryParams,
  isTempraroryModelVisible,
  isTempraroryDrawerVisible,
  TempraroryFilterInputChangeValue,
  getReportList
})(stockTransferTemprarory);
