import React, { useEffect } from "react";
import {
  Card,
  Row,
  Col,
  Table,
  Space,
  Button,
  Popconfirm,
  Spin,
  Pagination,
} from "antd";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  getStockTransferTempraroryById,
  isTempraroryViewModelVisible,
  isTempraroryModelVisible,
  stockTempraroryEdit,
  stockTempraroryDelete,
  getStockTempraroryList,
  setStockTempraroryParams,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import moment from "moment";

function StockTransferTempraroryList(props) {
  const { stockTempraroryList } = props;

  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  const columns = [
    {
      title: "S.No",
      width: 70,
      fixed: "left",
      dataIndex: "index",
    },
    {
      title: "Code",
      render: (row) => <span className={columnClass}>{row.code}</span>,
      width: 120,
      fixed: "left",
    },
    {
      title: "Requester Name",
      width: 100,
      render: (row) => <span className={columnClass}>{row.requestername}</span>,
    },
    {
      title: "To Counter",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.tocounter?.name}</span>
      ),
    },
    {
      title: "Out Time",
      width: 150,
      render: (row) => (
        <span className={columnClass}>
          {moment(row?.out_time).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "In Time",
      width: 150,
      render: (row) => (
        <span className={columnClass}>
          {moment(row?.in_time).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "Vehicle No",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.purchase?.vehicle?.vehicleno}</span>
      ),
    },
    {
      title: "Description",
      width: 100,
      render: (row) => <span className={columnClass}>{row.description}</span>,
    },

    {
      title: "Actions",
      key: "action",
      width: 100,
      fixed: "right",
      render: (row, record) => (
        <Space size="middle">
          <>
            <i
              className="ri-eye-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.getStockTransferTempraroryById(row.id);
                props.isTempraroryViewModelVisible(true);
              }}
            ></i>

            {row.in_time == null && (
              <>
                {props.userAccessList?.indexOf(
                  "StockTransfer.change_temprarorytransfer"
                ) !== -1 && (
                  <i
                    className="ri-edit-box-line hp-mr-2 "
                    style={{ cursor: "pointer", color: "#fc6f03" }}
                    onClick={() => {
                      props.stockTempraroryEdit(row);
                      props.isTempraroryModelVisible(true);
                    }}
                  ></i>
                )}
                {props.userAccessList?.indexOf(
                  "StockTransfer.delete_temprarorytransfer"
                ) !== -1 && (
                  <Popconfirm
                    placement="topRight"
                    title="Are you sure to delete this Record?"
                    onConfirm={() => props.stockTempraroryDelete(row.id)}
                    okText="Yes"
                    cancelText="No"
                    className="hp-ml-8"
                    icon={
                      <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
                    }
                  >
                    <i
                      className="ri-delete-bin-2-line "
                      style={{ cursor: "pointer", color: "#ff0000" }}
                    ></i>
                  </Popconfirm>
                )}
              </>
            )}
          </>
        </Space>
      ),
    },
  ];

  const data = stockTempraroryList.map((row, key) => ({
    ...row,
    // index: key + 1,
    index:
      (props.stockTempraroryparams.page - 1) *
        props.stockTempraroryparams.page_size +
      (key + 1),
  }));

  function onPageChange(page, pageSize) {
    props.setStockTempraroryParams({
      ...props.stockTempraroryparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getStockTempraroryList({
      ...props.stockTempraroryparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  return (
    <Card>
      <Row className="hp-mb-12">
        <Col span={24}>
          <Spin tip="Loading..." spinning={props.loading} delay={500}>
            <Table
              columns={columns}
              rowClassName="hp-btn-bg-color-none"
              dataSource={data}
              scroll={{ x: 1300 }}
              pagination={false}
              size="middle"
            />
          </Spin>
        </Col>
      </Row>
    </Card>
  );
}

function mapStateToProps({ stockRequest, system }) {
  return {
    noofpages: stockRequest.noofpages,
    stockTemprarorylistCount: stockRequest.stockTemprarorylistCount,
    stockTempraroryViewModel: stockRequest.stockTempraroryViewModel,
    stockTempraroryList: stockRequest.stockTempraroryList,
    addTempraroryModel: stockRequest.addTempraroryModel,
    loading: stockRequest.loading,
    userAccessList: system.userAccessList,
    stockTempraroryparams: stockRequest.stockTempraroryparams,
  };
}

export default connect(mapStateToProps, {
  getStockTransferTempraroryById,
  isTempraroryViewModelVisible,
  isTempraroryModelVisible,
  stockTempraroryEdit,
  stockTempraroryDelete,
  getStockTempraroryList,
  setStockTempraroryParams,
})(StockTransferTempraroryList);
