import { Fragment, useState } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Modal, Row, Col, List, Popconfirm, Form, Input } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";

import {
  isTempraroryViewModelVisible,
  stockTransferTemprarory,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import moment from "moment";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const stockTransferTempraroryModel = (props) => {
  const [remarks, setRemarks] = useState({});

  const [form] = Form.useForm();

  const handleResetInputValues = () => {
    form.resetFields();
    props.isTempraroryViewModelVisible(false);
  };

  function stockInTemprarory() {
    props.stockTransferTemprarory({
      id: props.stockTransferTempraroryDetails.id,
    });
    form.resetFields();
  }

  const stockTransferTempraroryList = [
    {
      title: "REQUESTER NAME",
      description:
        props.stockTransferTempraroryDetails.requestername == null
          ? "none"
          : props.stockTransferTempraroryDetails.requestername,
    },
    {
      title: "TO COUNTER",
      description:
        props.stockTransferTempraroryDetails.tocounter?.name == null
          ? "none"
          : props.stockTransferTempraroryDetails.tocounter?.name,
    },
    {
      title: "FROM COUNTER",
      description:
        props.stockTransferTempraroryDetails.counter?.name == null
          ? "none"
          : props.stockTransferTempraroryDetails.counter?.name,
    },
    {
      title: "VEHICLE NUMBER",
      description:
        props.stockTransferTempraroryDetails.purchase?.vehicle?.vehicleno ==
        null
          ? "none"
          : props.stockTransferTempraroryDetails.purchase?.vehicle?.vehicleno,
    },
    {
      title: "VERSION INFO",
      description:
        props.stockTransferTempraroryDetails.purchase?.vehicle?.version?.name ==
        null
          ? "none"
          : props.stockTransferTempraroryDetails.purchase?.vehicle?.version
              ?.name,
    },
    {
      title: "OUT TIME",
      description:
        props.stockTransferTempraroryDetails.out_time == null
          ? "none"
          : moment(props.stockTransferTempraroryDetails.out_time).format(
              "DD-MM-YYYY, h:mm:ss a"
            ),
    },
    {
      title: "IN TIME",
      description:
        props.stockTransferTempraroryDetails.in_time == null
          ? "none"
          : moment(props.stockTransferTempraroryDetails.in_time).format(
              "DD-MM-YYYY, h:mm:ss a"
            ),
    },
    {
      title: "DESCRIPTION",
      description:
        props.stockTransferTempraroryDetails.description == null
          ? "none"
          : props.stockTransferTempraroryDetails.description,
    },
  ];

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Popconfirm
          disabled={
            props.stockTransferTempraroryDetails.in_time == null ? false : true
          }
          placement="topRight"
          title="Are you sure to Submit this Record?"
          onConfirm={() => stockInTemprarory()}
          okText="Yes"
          cancelText="No"
          icon={
            <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
          }
        >
          <Button
            block
            className="hp-px-12"
            type="primary"
            disabled={
              props.stockTransferTempraroryDetails.in_time == null
                ? false
                : true
            }
          >
            Received
          </Button>
        </Popconfirm>
      </Fragment>
    );
  };

  // const FooterCancel = () => {
  //   return (
  //     <Fragment>
  //       <Popconfirm
  //         placement="topRight"
  //         title="Are you sure to Reject this Record?"
  //         onConfirm={() => stockInApprove(3)}
  //         okText="Yes"
  //         cancelText="No"
  //         className="hp-ml-8"
  //         icon={
  //           <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
  //         }
  //       >
  //         <Button block className="hp-px-12 hp-reject-btn hp-font-weight-400">
  //           Received
  //         </Button>
  //       </Popconfirm>
  //     </Fragment>
  //   );
  // };

  const modalTitle = (
    <h5 className="modal-title">Stock Transfer Temporary View</h5>
  );

  return (
    <Modal
      visible={props.stockTempraroryViewModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isTempraroryViewModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <>
          {props.userAccessList?.indexOf(
            "StockTransfer.change_temprarorytransfer"
          ) !== -1 && (
            <Row justify="center">
              <Col span={12} className="hp-pl-4">
                <FooterAddOrUpdate />
              </Col>

              {/* <Col span={12} className="hp-pr-4">
                  <FooterCancel />
                </Col> */}
            </Row>
          )}
        </>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={516}
    >
      <Row>
        <Col span={24} md={24}>
          <List
            grid={{
              gutter: 16,
              xs: 1,
              sm: 2,
              md: 3,
              lg: 3,
              xl: 3,
              xxl: 3,
            }}
            dataSource={stockTransferTempraroryList}
            renderItem={(item) => (
              <List.Item>
                <List.Item.Meta
                  title={<span>{item.title}</span>}
                  description={<span>{item.description}</span>}
                />
              </List.Item>
            )}
          />
        </Col>
      </Row>
    </Modal>
  );
};

function mapStateToProps({ stockRequest, system }) {
  return {
    stockTempraroryViewModel: stockRequest.stockTempraroryViewModel,
    stockTransferTempraroryDetails: stockRequest.stockTransferTempraroryDetails,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  isTempraroryViewModelVisible,
  stockTransferTemprarory,
})(stockTransferTempraroryModel);
